import Vue from 'vue'
import Router from 'vue-router'

import Archive from '@/components/front/archive'
import Page from '@/components/front/page'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'page/1',
      component: index,
      children:[
        {path: 'page/:id',name:'page',component:Page},
        {path: 'archive/:id',name:'archive',component:Archive}
      ]
    }
  ]
})



