<template>
  <div id="commit">
      <div v-if="existence">
          <div>一些评论们。。</div>
          <div v-for="commitlist in commitlists">
              <img :src="commitlist.commit.avatar_url" width="80" height="80" :alt="commitlist.commit.login">
          <div>
              <b>{{commitlist.commit.login}}</b>
              评论于{{commitlist.commit.commit_at}}
          </div>
          <p>
              {{commitlist.commit.body}}
          </p>
          </div>
          <p>
            评论施工中。。。如果想要评论，请到<a :href="issueurl">这里</a>评论
          </p>
      </div>
      <div v-else>
          <div>未开启评论</div>
      </div>
  </div>
</template>

<script>
import marked from "marked";
export default {
  data() {
    return {
      commitlists: [],
      existence: false,
      issueid: 0,
      issueurl: ""
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init: function() {
      var that = this;
      this.$http({
        method: "get",
        url: "https://api.github.com/repos/FireUnicornser/govBlog/issues"
      }).then(function(response) {
        for (var i = 0; i <= response.data.length; i++) {
          if (response.data[i]["title"] == that.$route.params.id) {
            that.existence = true;
            that.issueid = response.data[i]["number"];
            that.issueurl = response.data[i]["html_url"];
            that
              .$http({
                method: "get",
                url:
                  "https://api.github.com/repos/FireUnicornser/govBlog/issues/" +
                  that.issueid +
                  "/comments"
              })
              .then(function(response) {
                for (var i = 0; i <= response.data.length; i++) {
                  that.commitlists.push({
                    commit: {
                      avatar_url: response.data[i]["user"]["avatar_url"],
                      login: response.data[i]["user"]["login"],
                      commit_at: response.data[i]["created_at"],
                      body: response.data[i]["body"]
                    }
                  });
                }
              });
          }
        }
      });
    }
  }
};
</script>

<style>

</style>



