<template>
  <div id="main">
      <div v-for="bloglist in bloglists">
        <router-link :to="{path:'/archive/'+bloglist.blog.hash}">>>{{bloglist.blog.title}}</router-link>
        <div>{{bloglist.blog.abs}}</div>
        <router-link :to="{path:'/archive/'+bloglist.blog.hash}">more>></router-link>
      </div>
      <router-link v-if="before" :to="(Number(this.$route.params.id)-1).toString()" @click.native="chageurl()">before</router-link>
      <router-link v-if="next" :to="(Number(this.$route.params.id)+1).toString()" @click.native="chageurl()">next</router-link>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bloglists: [],
      blogurl: "/blogs/",
      next: false,
      before: false,
      blogtotal: 0,
      blogsbuffer: []
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init: function() {
      var that = this;
      this.$http({
        method: "get",
        baseURL: this.blogurl,
        url: "blogs.json"
      }).then(function(response) {
        that.blogtotal = response.data.length;
        that.blogsbuffer = response.data;
        that.chageurl();
      });
    },
    chageurl: function() {
      if (Number(this.$route.params.id) <= 1) {
        this.before = false;
      } else {
        this.before = true;
      }
      if (Number(this.$route.params.id * 10) >= this.blogtotal) {
        this.next = false;
      } else {
        this.next = true;
      }
      this.chagelist();
    },
    chagelist: function() {
      this.bloglists = [];
      for (
        var i = (this.$route.params.id - 1) * 10;
        i< (this.$route.params.id - 1) * 10 + 10 && i < this.blogtotal;
        i++
      ) {
        this.bloglists.push({ blog: this.blogsbuffer[i] });
      }
    }
  }
};
</script>

<style>

</style>



