<template>
  <div id="main">
      <div v-html="blogbody">
      </div>
      
      <hr>
      <Commit></Commit>

  </div>
</template>

<script>
import marked from "marked";
import Commit from "./commit";
export default {
  data() {
    return {
      blogbody: "",
      blogurl: "/blogs/"
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init: function() {
      var that = this;
      this.$http({
        method: "get",
        baseURL: this.blogurl,
        url: that.$route.params.id + ".md"
      }).then(function(response) {
        that.blogbody = marked(response.data);
      });
    }
  },
  components: {
    Commit
  }
};
</script>

<style>

</style>



